import { hot } from 'react-hot-loader';
import { Link } from 'react-router-dom';
import LinkExternal from '../components/Link/LinkExternal';
import { nftMainnetAddress, rnctMainnetAddress, metadataStoreMainnetAddress, traitsMainnetAddress, } from '../configs';

import './ScreenTerms.scss';



function ScreenSmartContracts() {
  return (
    <section className="section-terms">
      <div className="container" data-aos="fade-in" data-delay="200">
        <div className="pre-title">
          <Link className="pre-title-back" to="/">&larr; Go to homepage</Link>
        </div>

        <div className="row">
          <div className="col-12 col-lg-10 offset-lg-1 col-xl-8 offset-xl-2">
            <h1 className="title">Roborovski NFT Smart Contracts</h1>
            <p></p>

            <br />
            <div className="textpage">
              <h2 className="title">Roborovski NFT</h2>
              <p><LinkExternal href={`https://etherscan.io/token/${nftMainnetAddress}`}>{nftMainnetAddress}</LinkExternal></p>

              <h2 className="title">Roborovski Name Change Token</h2>
              <p><LinkExternal href={`https://etherscan.io/token/${rnctMainnetAddress}`}>{rnctMainnetAddress}</LinkExternal></p>

              <h2 className="title">Roborovski Metadata Store</h2>
              <p><LinkExternal href={`https://etherscan.io/address/${metadataStoreMainnetAddress}`}>{rnctMainnetAddress}</LinkExternal></p>

              <h2 className="title">Roborovski Traits</h2>
              <p><LinkExternal href={`https://etherscan.io/address/${traitsMainnetAddress}`}>{rnctMainnetAddress}</LinkExternal></p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default hot(module)(ScreenSmartContracts);
