import { hot } from 'react-hot-loader';
import IncrementIcon from '../components/SvgRobo/IncrementIcon';
import DecrementIcon from '../components/SvgRobo/DecrementIcon';



const MintModalComponentSelect = ({ data }) => {
  const onChange = (e) => {
    let newCount = parseInt(e.target.value);
    if (isNaN(newCount)) newCount = 0;
    if (newCount < 0) newCount = 0;
    if (newCount > data.maxCountLimitTx) newCount = data.maxCountLimitTx;
    data.setCount(newCount);
  }
  const increment = () => {
    let newCount = data.count + 1;
    if (newCount > data.maxCountLimitTx) newCount = data.maxCountLimitTx;
    data.setCount(newCount);
  }
  const decrement = () => {
    let newCount = data.count - 1;
    if (newCount < 0) newCount = 0;
    data.setCount(newCount);
  }

  return (
    <div className="modal-select">
      <div className="sel">
        <div className="sel-item" onClick={decrement} ><DecrementIcon /></div>
        <input
          type="number"
          value={data.count}
          onChange={onChange}
        />
        <div className="sel-item" onClick={increment}><IncrementIcon /></div>
      </div>
      <div className="sel-desc">Amount max {data.maxCountLimitTx} per transaction / {data.maxCountLimit} per address</div>
    </div>
  );
};

export default hot(module)(MintModalComponentSelect);
