import { useEffect } from 'react';
import { hot } from 'react-hot-loader';
import Modal, { useModalState } from 'react-simple-modal-provider';
import { toast } from 'react-toastify';

import CloseIcon from '../components/SvgRobo/CloseIcon';
import MetamaskIcon from '../components/SvgRobo/MetamaskIcon';
import WalletConnectIcon from '../components/SvgRobo/WalletConnectIcon';
import './WalletConnectModal.scss';

import useAuth from '../hooks/useAuth';


async function switchEthereumChain() {
  try {
    return await window.ethereum.request({
      method: 'wallet_switchEthereumChain',
      params: [{ chainId: '0x1', }]
    });
  } catch (switchError) {
    toast.error('Please switch MetaMask to the Ethereum Mainnet');
  }
}

const WalletConnectModal = ({ children }) => {
  const [isOpen, setOpen] = useModalState();
  useEffect(() => {
    if (isOpen) {
      document.body.classList.add("no-scroll");
    } else {
      document.body.classList.remove("no-scroll");
    }
  }, [isOpen]);
  const onClose = () => {
    setOpen(false);
  }

  const { login } = useAuth();
  const connectorLocalStorageKey = 'connectorId';
  const onConnectToMetamask = () => {
    const connectorId = 'injected';
    login(connectorId);
    window.localStorage.setItem(connectorLocalStorageKey, connectorId);
    switchEthereumChain();
    setOpen(false);
  }
  const onConnectToWalletConnect = () => {
    const connectorId = 'walletconnect';
    login(connectorId);
    window.localStorage.setItem(connectorLocalStorageKey, connectorId);
    setOpen(false);
  }

  return (
    <Modal
      id={'WalletConnectModal'}
      consumer={children}
      isOpen={isOpen}
      setOpen={setOpen}
      draggable={false}
      duration={300}
    >
      <div className="modal-mask">
        <div className="modal-wrapper">
          <div className="modal-overlay" onClick={onClose}></div>
          <div className="modal-container">

            <div className="modal-body">
              <div className="modal-header">Connect wallet</div>

              <div className="sel__res">
                <div className="sel__res-col sel__res-col--1">
                  <button style={{ width: '100%', height: '100%', padding: '1.3rem', backgroundColor: '#fbbd74', }}
                    onClick={onConnectToMetamask}
                  >
                    <span className='secondary'><MetamaskIcon /></span> Metamask
                  </button>
                </div>
                <div className="sel__res-col sel__res-col--2">
                  <button style={{ width: '100%', height: '100%', padding: '1.3rem', backgroundColor: '#fbbd74', }}
                    onClick={onConnectToWalletConnect}
                  >
                    <span className='secondary'><WalletConnectIcon /></span> Wallet Connect
                  </button>
                </div>
              </div>
            </div>

            <button className="modal-close-button" onClick={onClose}><CloseIcon /></button>
          </div>
        </div>
      </div>
    </Modal>
  );
};

export default hot(module)(WalletConnectModal);
