import { useState, useEffect } from 'react';
import { hot } from 'react-hot-loader';
import Modal, { useModalState, useModalProps } from 'react-simple-modal-provider';
import { utils } from 'ethers';
import { toast } from 'react-toastify';
import ButtonPrimary from '../components/ButtonPrimary';
import CloseIcon from '../components/SvgRobo/CloseIcon';
import { useEagerConnect, useActiveWeb3React } from '../hooks/index';
import getNftContract from '../hooks/getNftContract';
import getNftAddress from '../hooks/getNftAddress';
import getRnctContract from '../hooks/getRnctContract';
import LinkExternal from '../components/Link/LinkExternal';
import './MintModal.scss';



const ModalBody = ({ name, onChange, }) => {
  const { tokenId, Surname } = useModalProps("ChangeNameModal");
  const triedEager = useEagerConnect();
  const context = useActiveWeb3React();
  const { library, chainId, account, active } = context;


  const etherscan = `https://${chainId === 4 ? 'rinkeby.' : ''}etherscan.io`;
  const onChangeName = async () => {
    if (name === '') {
      return;
    }
    try {
      const contract = getNftContract(chainId, library, true);
      const contract3 = getRnctContract(chainId, library);
      if (contract) {
        let balance = await contract3.balanceOf(account);
        balance = parseFloat(utils.formatEther(balance));
        balance = parseInt(balance);
        if (balance < 1830) {
          toast.error('Insufficient fund. Required 1,830 RNCT.');
          return;
        }

        const tx = await contract.changeName(tokenId, name);
        toast.info(<LinkExternal href={etherscan + '/tx/' + tx.hash}>Pending. tx: {tx.hash.substring(0, 6)}...</LinkExternal>);
        await tx.wait(1);
        toast.success(<LinkExternal href={etherscan + '/tx/' + tx.hash}>Done. tx: {tx.hash.substring(0, 6)}...</LinkExternal>);
      }
    } catch (error) {
      console.log('---', error)
      toast.error('Error.');
    }
  }

  return (
    <div className="modal-body">
      <div className="modal-header"><span style={{ color: 'rgb(251, 189, 116)' }}>{Surname} #{tokenId}</span> Change Name</div>

      <br />
      <div className="modal-select">
        <div className="sel">
          <input
            type="text"
            size='80'
            pattern='^[A-Za-z]{1}[A-Za-z0-9 ]{0,24}$'
            value={name}
            onChange={onChange}
          />
        </div>
        <div className="sel-desc">Type new name. Max length: 25. Format: A-Za-z0-9 and 'space'.</div>
      </div>
      <ButtonPrimary tag="button" label="Change Name" size="full" onClick={onChangeName} />
    </div>
  );
}

const ChangeNameModal = ({ children }) => {
  const [isOpen, setOpen] = useModalState();
  useEffect(() => {
    if (isOpen) {
      document.body.classList.add("no-scroll");
    } else {
      document.body.classList.remove("no-scroll");
    }
  }, [isOpen]);
  const [name, setName] = useState('');
  const onClose = () => {
    setOpen(false);
    setName('');
  }

  const onChange = (e) => {
    let newName = e.target.value;
    if (newName === '')
      setName(newName);

    let fail = !/^[A-Za-z]{1}[A-Za-z0-9 ]{0,24}$/.test(newName);
    if (!fail)
      setName(newName);
  }


  return (
    <Modal
      id={'ChangeNameModal'}
      consumer={children}
      isOpen={isOpen}
      setOpen={setOpen}
      draggable={false}
      duration={300}
      width={300}
    >
      <div className="modal-mask">
        <div className="modal-wrapper">
          <div className="modal-overlay" onClick={onClose}></div>
          <div className="modal-container" >
            <ModalBody name={name} onChange={onChange} />
            <button className="modal-close-button" onClick={onClose}><CloseIcon /></button>
          </div>
        </div>
      </div>
    </Modal>
  );
};

export default hot(module)(ChangeNameModal);
