import { hot } from 'react-hot-loader';
import Number from 'react-number-format';



const MintModalComponentHeader = ({ data }) => {
  const { totalSupply, maxSupply, stage } = data;
  return (
    <div className="modal-header" style={{ marginTop: '-22px' }}>
      <Number className='secondary' value={totalSupply} displayType={'text'} thousandSeparator={true} /> / {" "}
      <Number value={maxSupply} displayType={'text'} thousandSeparator={true} />{" "}
      <span className='secondary'>{stage}</span>
    </div>
  );
};

export default hot(module)(MintModalComponentHeader);
