function ComponentSale() {
  const [time, setTime] = useState(0);
  useEffect(() => {
    setTime(now());
    const intervalId = setInterval(() => {
      setTime(now());
    }, 1000);
    return () => {
      clearInterval(intervalId);
    }
  }, []);

  const timestampPresale = useSelector(S.nft.getTimestampPresale);
  const timestampPresaleEnd = useSelector(S.nft.getTimestampPresaleEnd);
  //const timestampRaffleEnd = useSelector(S.nft.getTimestampRaffleEnd);
  const timestampSale = useSelector(S.nft.getTimestampSale);
  const [state, setState] = useState({
    isPresaleOpen: false, isPresalePaused: false, isSaleOpen: false,
  });
  useEffect(() => {
    setState({
      isPresaleOpen: time >= timestampPresale && time < timestampPresaleEnd,
      isPresalePaused: time >= timestampPresaleEnd && time < timestampSale,
      isSaleOpen: time >= timestampSale,
    })
  }, [time]);

  return (
    <div
      className={(!state.isPresaleOpen && !state.isPresalePaused && !state.isSaleOpen) ? "main__countdown-item" : "main__countdown-item-soldout"}
      style={(!state.isPresaleOpen && !state.isPresalePaused && !state.isSaleOpen) ? {} : { backgroundColor: '#fe5c50' }}
      id={`time-${time}`}
    >
      {!state.isPresaleOpen && !state.isPresalePaused && !state.isSaleOpen && <CountDownTimer />}
      {/*!state.isPresaleOpen && !state.isPresalePaused && !state.isSaleOpen && <ComponentCommingSoon />*/}

      {state.isPresaleOpen && <ComponentPresaleIsOpen />}
      {state.isPresalePaused && <ComponentRaffleIsOpen />}
      {state.isSaleOpen && <ComponentSaleIsOpen />}
    </div>
  );
}
