import { hot } from 'react-hot-loader';
import { Link } from 'react-router-dom';
import LinkExternal from '../components/Link/LinkExternal';
import { openSeaUrl } from '../configs';

import './ScreenHowToBuy.scss';



function ScreenHowToBuy() {
  return (
    <section className="section-howtobuy">
      <div className="container" data-aos="fade-in" data-delay="200">
        <div className="pre-title">
          <Link className="pre-title-back" to="/">&larr; Go to homepage</Link>
        </div>

        <div className="row">
          <div className="col-12 col-lg-10 offset-lg-1 col-xl-8 offset-xl-2">
            <h1 className="title">How to buy</h1>
            <div className="textpage">
              <p>Follow these instructions</p>
              <h2 className="title">SETUP WALLET</h2>
              <p>You will need <LinkExternal href="https://metamask.io/">MetaMask</LinkExternal> to mint one of our Roborovski NFTs. Install the chrome extension or download the mobile app to get started. Please reference <LinkExternal href="https://www.one37pm.com/nft/tech/how-to-set-up-metamask-wallet">this article</LinkExternal> to learn how to set up MetaMask. If you are curious, other wallet options <LinkExternal href="https://ethereum.org/en/wallets/">can be found here.</LinkExternal></p>
              <h2 className="title">BUY ETH</h2>
              <p>Our Roborovski NFT price is TBA, but you will need to pay extra transaction fee (referred to as gas) to the Ethereum network to mint the NFT. You can directly deposit to your MetaMask wallet or use a popular exchange like <LinkExternal href="https://www.coinbase.com/">Coinbase</LinkExternal>, <LinkExternal href="https://www.gemini.com/">Gemini</LinkExternal>, or a service like <LinkExternal href="https://www.gemini.com/">MoonPay</LinkExternal> to acquire a small amount of ETH. Typically 0.1 ETH will be more than sufficient for gas, but <LinkExternal href="https://etherscan.io/gastracker">gas can vary</LinkExternal> depending on how busy the network is.</p>
              <h2 className="title">TRANSFER ETH</h2>
              <p>You may need to transfer ETH from Coinbase or Gemini to your MetaMask wallet. Please follow their guides for sending ETH to your wallet address (starts with 0x). Once transferred, you will be ready to  mint your ROBO!</p>

              <h2 className="title">BUY ON OPENSEA</h2>
              <p>Go to the Roborovski NFT page on the OpenSea. And buy the one you like.
                <br />
                <LinkExternal className="footer__links-item" href={openSeaUrl}>https://opensea.io/collection/roborovski-nft</LinkExternal>
              </p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default hot(module)(ScreenHowToBuy);
