const WalletAccountModal = ({ children }) => {
  const [isOpen, setOpen] = useModalState();
  const onClose = () => {
    setOpen(false);
  }
  useEffect(() => {
    if (isOpen) {
      document.body.classList.add("no-scroll");
    } else {
      document.body.classList.remove("no-scroll");
    }
  }, [isOpen]);
  const blockNumber = useSelector(S.provider.getBlockNumber);
  const isSaleOpen = useSelector(S.nft.getSaleOpen);
  const isSoldOut = useSelector(S.nft.getSoldOut);

  const triedEager = useEagerConnect();
  const context = useActiveWeb3React();
  const { library, chainId, account, active } = context;
  const { logout } = useAuth();

  const etherscan = `https://${chainId === 4 ? 'rinkeby.' : ''}etherscan.io`;

  const connectorLocalStorageKey = 'connectorId';
  const handleLogout = () => {
    logout();
    window.localStorage.removeItem(connectorLocalStorageKey);
    setOpen(false);
  }

  const handleCopyAddress = () => {
    copy(account);
    toast.info('Address copied!');
  }

  const [balance, setBalance] = useState(0);
  const [tokenIds, setTokenIds] = useState([]);
  //const [unclaimedRoyalty, setUnclaimedRoyalty] = useState('0');
  const [unclaimedRnct, setUnclaimedRnct] = useState('0');

  const [isUpdating, setUpdating] = useState(false);
  useEffect(async () => {
    if (isUpdating) return;
    setUpdating(true);

    if (isOpen && account) {
      try {
        const contract = getNftContract(chainId, library);
        if (contract) {
          let balance = await contract.balanceOf(account);
          balance = balance.toNumber();
          setBalance(balance);
          if (balance > 0) {
            let tokenIds = [];
            if (balance < 20) {
              tokenIds = await contract.tokensOfOwner(account);
            } else {
              let i = 0;
              while (i < balance) {
                tokenIds.push(
                  await contract.tokenOfOwnerByIndex(account, i)
                );
                i++;
              }
            }
            setTokenIds(tokenIds);

            //let royaltyTotal = 0;
            let rnctTotal = 0;
            for (let i in tokenIds) {
              /*const contract2 = getRoyaltyDistributorContract(chainId, library);
              let royalty = await contract2.accumulated(nullAddress, tokenIds[i]);
              royaltyTotal += parseFloat(utils.formatEther(royalty));*/
              const contract3 = getRnctContract(chainId, library);
              let rnct = await contract3.accumulated(tokenIds[i]);
              rnctTotal += parseFloat(utils.formatEther(rnct));
            }
            /*const contract3 = getRnctContract(chainId, library);
            try {
              let rnct = await contract3.accumulatedBatch(tokenIds);
              rnctTotal = parseFloat(utils.formatEther(rnct));
            } catch (error) {
            }*/
            //setUnclaimedRoyalty(royaltyTotal);
            setUnclaimedRnct(rnctTotal);
          } else {
            //setUnclaimedRoyalty('0');
            setUnclaimedRnct('0');
          }
        } else {
          setBalance(0);
          //setUnclaimedRoyalty('0');
          setUnclaimedRnct('0');
        }
      } catch (error) { /*console.error(error);*/ }
    }

    setUpdating(false);
  }, [isOpen, account, blockNumber]);


  /*const handleClaimRoyalty = async () => {
    if (account && parseFloat(unclaimedRoyalty) > 0) {
      try {
        const contract = getNftContract(chainId, library);
        if (contract) {
          const balance = await contract.balanceOf(account);
          if (balance.toNumber() === 0) return;
          const tokenIds = await contract.tokensOfOwner(account);

          const contract2 = getRoyaltyDistributorContract(chainId, library, true);
          const tx = await contract2.claim(nullAddress, tokenIds);
          toast.info(<LinkExternal href={etherscan + '/tx/' + tx.hash}>Pending. tx: {tx.hash.substring(0, 6)}...</LinkExternal>);
          await tx.wait(1);
          toast.success(<LinkExternal href={etherscan + '/tx/' + tx.hash}>Done. tx: {tx.hash.substring(0, 6)}...</LinkExternal>);

          setUnclaimedRoyalty('0');
        }
      } catch (error) {
        if (typeof error === 'string' && error.includes('insufficient funds')) {
          toast.error('Insufficient funds');
        } else if (typeof error === 'object' && error.message.includes('insufficient funds')) {
          toast.error('Insufficient funds');
        } else {
          //console.error(error);
          toast.error('Error');
        }
      }
    }
  }*/

  const handleClaimRnct = async () => {
    if (account && parseFloat(unclaimedRnct) > 0) {
      try {
        const contract = getNftContract(chainId, library);
        if (contract) {
          const balance = await contract.balanceOf(account);
          if (balance.toNumber() === 0) return;
          //const tokenIds = await contract.tokensOfOwner(account);

          const contract2 = getRnctContract(chainId, library, true);
          const tx = await contract2.claim(tokenIds);
          toast.info(<LinkExternal href={etherscan + '/tx/' + tx.hash}>Pending. tx: {tx.hash.substring(0, 6)}...</LinkExternal>);
          await tx.wait(1);
          toast.success(<LinkExternal href={etherscan + '/tx/' + tx.hash}>Done. tx: {tx.hash.substring(0, 6)}...</LinkExternal>);

          setUnclaimedRnct('0');
        }
      } catch (error) {
        if (typeof error === 'string' && error.includes('insufficient funds')) {
          toast.error('Insufficient funds');
        } else if (typeof error === 'object' && error.message.includes('insufficient funds')) {
          toast.error('Insufficient funds');
        } else {
          //console.error(error);
          toast.error('Error');
        }
      }
    }
  }

  const [isWL, setWL] = useState(false);
  useEffect(async () => {
    if (account && !isSaleOpen && !isSoldOut) {
      try {
        const res = await api.isWhitelisted(account);
        if (res.ok) {
          return setWL(res.result);
        } else {
          setWL(false);
        }
      } catch (error) { setWL(false); }
    } else {
      setWL(false);
    }
  }, [account]);

  const { open: openVideoModal } = useModal('VideoModal');

  const closeAndShow = () => {
    //const fancybox = new NativeFancybox([{ src: youtubeShortFilmUrl }]);
    openVideoModal();
    onClose();
  }

  return (
    <Modal
      id={'WalletAccountModal'}
      consumer={children}
      isOpen={isOpen}
      setOpen={setOpen}
      draggable={false}
      duration={300}
    >
      <div className="modal-mask">
        <div className="modal-wrapper">
          <div className="modal-overlay" onClick={onClose}></div>
          <div className="modal-container">

            <div className="modal-body">
              <div className="modal-header">Your Wallet {isWL ? <span style={{ color: '#fbbd74' }}>(Whitelisted)</span> : null}</div>
              <div className='address' style={{ color: '#fbbd74', cursor: 'pointer', }}
                onClick={handleCopyAddress}
              >{account}</div>
              <div className="sel__res">
                <div className="sel__res-col sel__res-col--1">
                  <LinkExternal href={`https://etherscan.io/address/${account}`}>
                    <button
                      style={{ width: '100%', height: '100%', padding: '1.3rem', backgroundColor: '#fbbd74', }}
                    >View on Etherscan</button>
                  </LinkExternal>
                </div>
                <div className="sel__res-col sel__res-col--2">
                  <Link to='/wallet'>
                    <button
                      style={{ width: '100%', height: '100%', padding: '1.3rem', backgroundColor: '#fbbd74', }}
                      onClick={() => setOpen(false)}
                    >Open Wallet</button>
                  </Link>
                </div>
              </div>


              {balance > 0 && <div className="sel__res">
                <div className="sel__res-col sel__res-col--1">
                  <button
                    //data-fancybox=""
                    //data-src={youtubeShortFilmUrl}
                    onClick={closeAndShow}
                    style={{ width: '100%', height: '100%', padding: '1.3rem', backgroundColor: '#fbbd74', }}
                  >Watch short film</button>
                </div>
                <div className="sel__res-col sel__res-col--2">
                  <LinkExternal href={'https://m.imdb.com/title/tt9855094/'}>
                    <button
                      style={{ width: '100%', height: '100%', padding: '1.3rem', backgroundColor: '#fbbd74', }}
                    >Please rate and leave a comment, it will help our project to grow</button>
                  </LinkExternal>
                </div>
              </div>}

              <div style={{ marginTop: '10px' }}>
                <div className="modal-header"><LinkExternal href={etherscan + '/token/' + getNftAddress(chainId) + `?a=${account}`}>
                  You own <span style={{ color: '#fbbd74', }}>{balance}</span> ROBO
                </LinkExternal>
                </div>
                <div className="sel__res">
                  <div className="sel__res-col sel__res-col--1">
                    <Number
                      style={{ color: 'rgb(251, 189, 116)' }}
                      value={unclaimedRnct} suffix=' RNCT'
                      displayType={'text'} thousandSeparator={true}
                      fixedDecimalScale={true} decimalScale={6}
                    />
                    <div>Unclaimed RNCT</div>
                  </div>
                  <div className="sel__res-col sel__res-col--2">
                    <button
                      style={{ width: '100%', height: '100%', padding: '1.3rem', backgroundColor: '#fbbd74', }}
                      onClick={handleClaimRnct}
                    >Claim RNCT</button>
                  </div>
                </div>

                {/*<div className="sel__res">
                  <div className="sel__res-col sel__res-col--1">
                    <Number
                      style={{ color: 'rgb(251, 189, 116)' }}
                      value={unclaimedRoyalty} suffix=' ETH'
                      displayType={'text'} thousandSeparator={true}
                      fixedDecimalScale={true} decimalScale={6}
                    />
                    <div>Unclaimed Royalty</div>
                  </div>
                  <div className="sel__res-col sel__res-col--2">
                    <button
                      style={{ width: '100%', height: '100%', padding: '1.3rem', backgroundColor: '#fbbd74', }}
                      onClick={handleClaimRoyalty}
                    >Claim Royalty</button>
                  </div>
                </div>*/}
              </div>

              {/*partner &&
                <div style={{ marginTop: '10px' }}>
                  <div className="modal-header">Referral program</div>
                  <div className="sel__res">
                    <div className="sel__res-col sel__res-col--1"
                      onClick={handleCopyReferralLink}
                    >
                      <span style={{ color: 'rgb(251, 189, 116)' }}>{referralLink}</span>
                      <div>Your referral link</div>
                    </div>
                    <div className="sel__res-col sel__res-col--2">
                      <button
                        style={{ width: '100%', height: '100%', padding: '1.3rem', backgroundColor: '#fbbd74', }}
                        onClick={() => openRefLinkQrCodeModal({ referralLink, })}
                      >Show QR code</button>
                    </div>
                  </div>
                  <div className="sel__res">
                    <div className="sel__res-col sel__res-col--1">
                      <Number
                        style={{ color: 'rgb(251, 189, 116)' }}
                        value={unclaimedRefRewards} suffix=' ETH'
                        displayType={'text'} thousandSeparator={true}
                        fixedDecimalScale={true} decimalScale={6}
                      />
                      <div>Unclaimed Rewards</div>
                    </div>
                    <div className="sel__res-col sel__res-col--2">
                      <button
                        style={{ width: '100%', height: '100%', padding: '1.3rem', backgroundColor: '#fbbd74', }}
                        onClick={handleClaimRefRewards}
                      >Claim Rewards</button>
                    </div>
                  </div>
                </div>
              */}
            </div>

            <button className="modal-close-button" onClick={onClose}><CloseIcon /></button>
          </div>
        </div>
      </div>
    </Modal>
  );
};

export default hot(module)(WalletAccountModal);
