import { useState, useEffect } from 'react';
import { hot } from 'react-hot-loader';
import { useSelector } from 'react-redux';
import Modal, { useModalState, useModal } from 'react-simple-modal-provider';
import { utils } from 'ethers';
//import Number from 'react-number-format';
//import { useHistory } from "react-router-dom";

import CloseIcon from '../components/SvgRobo/CloseIcon';

import ButtonPrimary from '../components/ButtonPrimary';
import Timer from '../components/Timer';

import rPopupImg from '../assets/images/popup/r-popup.png';
import rPopupX2Img from '../assets/images/popup/r-popup@2x.png';


import MintModalComponentSelect from './MintModalComponentSelect';
import MintModalComponentHeader from './MintModalComponentHeader';
import MintModalComponentPriceData from './MintModalComponentPriceData';

import MintModalBodySoldOut from './MintModalBodySoldOut';
import MintModalBodySaleNotOpen from './MintModalBodySaleNotOpen';
import MintModalBodyNotWL from './MintModalBodyNotWL';
import './MintModal.scss';


import { youtubeMintUrl } from '../configs';
import { Fancybox as NativeFancybox } from '@fancyapps/ui/dist/fancybox.esm.js';

import { useEagerConnect, useActiveWeb3React } from '../hooks/index';
import getNftContract from '../hooks/getNftContract';
import { v4 as uuidv4 } from 'uuid';
import * as S from '../store/selectors';

import { api } from '../store/configureStore';
import { toast } from 'react-toastify';


const domain = {
  name: 'Roborovski.org Mint Request',
};
const types = {
  MintRequest: [
    { name: 'id', type: 'string' },
    { name: 'timestamp', type: 'uint256' },
    { name: 'wallet', type: 'address' },
    { name: 'count', type: 'uint256' },
  ],
};

const now = () => Math.trunc(Date.now() / 1000);
function pause(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}



const TIMESTAMP_RAFFLE_2 = 1645333200; // 1645333200; // 2022-02-20T05:00:00.000Z



const MintModal = ({ children }) => {
  const { open: openAlertModal } = useModal('AlertModal');

  const [isOpen, setOpen] = useModalState();
  useEffect(() => {
    if (isOpen) {
      document.body.classList.add("no-scroll");
      //openAlertModal();
    } else {
      document.body.classList.remove("no-scroll");
    }
  }, [isOpen]);
  const onClose = () => {
    setOpen(false);
    setCount(0);
  }

  //let history = useHistory();
  const closeAndShow = () => {
    const fancybox = new NativeFancybox([{ src: youtubeMintUrl }]);
    onClose();
  }

  const triedEager = useEagerConnect();
  const context = useActiveWeb3React();
  const { library, chainId, account, active, error } = context;

  const { open: openWalletConnectModal } = useModal('WalletConnectModal');
  useEffect(() => {
    if (isOpen && !library) {
      openWalletConnectModal();
      onClose();
    }
  }, [isOpen, library]);


  const [time, setTime] = useState(0);
  useEffect(() => {
    setTime(now());
    const intervalId = setInterval(() => {
      setTime(now());
    }, 1000);
    return () => {
      clearInterval(intervalId);
    }
  }, []);

  const timestampPresale = useSelector(S.nft.getTimestampPresale);
  const timestampPresaleEnd = useSelector(S.nft.getTimestampPresaleEnd);
  //const timestampRaffleEnd = useSelector(S.nft.getTimestampRaffleEnd);
  const timestampSale = useSelector(S.nft.getTimestampSale);

  const isSoldOut = useSelector(S.nft.getSoldOut);
  const [{ isPresaleOpen, isPresalePaused, isSaleOpen, nextRuffle, }, setState] = useState({
    isPresaleOpen: false, isPresalePaused: false, isSaleOpen: false,
  });
  /*useEffect(() => {
    if (isPresaleOpen) toast('PRESALE IS LIVE!', { autoClose: 5000, });
  }, [isPresaleOpen]);
  useEffect(() => {
    if (isPresalePaused) toast('RAFFLE IS LIVE!', { autoClose: 5000, });
  }, [isPresalePaused]);
  useEffect(() => {
    if (isSaleOpen) toast('SALE IS LIVE!', { autoClose: 5000, });
  }, [isSaleOpen]);
  useEffect(() => {
    if (isSoldOut) toast('SOLD OUT!', { autoClose: 5000, });
  }, [isSoldOut]);

  useEffect(() => {
    const timeNum = Math.floor((now() - TIMESTAMP_RAFFLE_2) / (15 * 60)) + 1;
    const nextRuffle = (TIMESTAMP_RAFFLE_2 + timeNum * 15 * 60);
    setState({
      isPresaleOpen: time >= timestampPresale && time < TIMESTAMP_RAFFLE_2,
      isPresalePaused: time >= TIMESTAMP_RAFFLE_2 && time < timestampSale,
      isSaleOpen: time >= timestampSale,
      timeNum,
      nextRuffle,
    });
  }, [time, timestampPresale, timestampPresaleEnd, timestampSale]);*/


  const blockNumber = useSelector(S.provider.getBlockNumber);

  const maxSupply = useSelector(S.nft.getMaxSupply);
  const totalSupply = useSelector(S.nft.getTotalSupply);
  const [lastTotalSupply, setLastTotalSupply] = useState(0);
  /*useEffect(() => {
    if (totalSupply > lastTotalSupply) {
      setLastTotalSupply(totalSupply);
      if (time >= timestampPresale) {
        toast(<div>
          MINTED: {" "}
          <Number className='secondary' value={totalSupply} displayType={'text'} thousandSeparator={true} /> / {" "}
          <Number value={maxSupply} displayType={'text'} thousandSeparator={true} />{" "}
        </div>);
      }
    }
  }, [totalSupply]);*/


  const priceEth = useSelector(S.nft.getPrice);

  const limitPresale = useSelector(S.nft.getLimitPresale);
  const limitSale = useSelector(S.nft.getLimitSale);
  //const limitTx = useSelector(S.nft.getLimitTx);


  const [maxCountLimit, setMaxCountLimit] = useState(0);
  const [maxCountLimitTx, setMaxCountLimitTx] = useState(0);
  const [minted, setMinted] = useState(0);
  const [count, setCount] = useState(0);
  const [balanceEth, setBalanceEth] = useState('0');

  const [totalEth, setTotalEth] = useState('0.00');
  //const [priceEth2, setPriceEth] = useState(priceSale);
  useEffect(() => {
    setTotalEth((count * priceEth).toFixed(6));
  }, [count, priceEth]);

  const [stage, setStage] = useState('-');
  useEffect(() => {
    if (isPresaleOpen) {
      setStage('PRESALE');
      setMaxCountLimit(limitPresale - minted);
      //if (limitTx > limitPresale - minted)
      setMaxCountLimitTx(limitPresale - minted);
      //else
      //  setMaxCountLimitTx(limitTx);
      //setPriceEth(pricePresale);
    }

    if (isPresalePaused) {
      setStage('RAFFLE');
      setMaxCountLimit(limitPresale - minted);
      //if (limitTx > limitPresale - minted)
      setMaxCountLimitTx(limitPresale - minted);
      //else
      //  setMaxCountLimitTx(limitTx);
      //setPriceEth(pricePresale);
    }

    if (isSaleOpen) {
      setStage('SALE');
      setMaxCountLimit(limitSale - minted);
      //if (limitTx > limitSale - minted)
      setMaxCountLimitTx(limitSale - minted);
      //else
      //  setMaxCountLimitTx(limitTx);
      //setPriceEth(priceSale);
    }
  }, [isPresaleOpen, isPresalePaused, isSaleOpen, minted]);


  useEffect(() => {
    if (library && account && !isSoldOut) {
      library.getBalance(account).then((balanceEthRaw) => {
        setBalanceEth(utils.formatEther(balanceEthRaw));
      }).catch(() => null);

      const contract = getNftContract(chainId, library);
      if (contract) {
        let step = 1;
        if (stage === 'RAFFLE')
          step = 1;
        else if (stage === 'SALE')
          step = 2;
        contract.mintedOf(step, account).then((mintedOf) => {
          setMinted(mintedOf.toNumber());
        }).catch(() => null);
      }
    }
  }, [stage, blockNumber, library, account]);


  const [mintReqStage, setMintReqStage] = useState(null);
  const handlerMint = async () => {
    if (chainId !== 1) {
      return;
    }
    if (count === 0) {
      return;
    }
    if (parseFloat(totalEth) >= parseFloat(balanceEth)) {
      toast.error('Insufficient funds');
      return;
    }
    if (totalSupply >= 7777) {
      return;
    }

    const signer = library.getSigner();
    const wallet = await signer.getAddress();
    const value = {
      id: uuidv4(),
      timestamp: now(),
      wallet,
      count,
    };
    try {
      setMintReqStage('Wallet signs Mint Request');
      const signature = await signer._signTypedData(domain, types, value);
      setMintReqStage('AntiSpam');
      await pause(500);
      const res = await api.sendMintRequest(value, signature);
      if (res.ok) {
        setMintReqStage('Done');
        if (res.result.signature) {
          const contract = getNftContract(chainId, library, true);
          setMintReqStage('Wallet signs Transaction');
          let tx;
          let value = utils.parseEther(totalEth);
          let gasPrice = await library.getGasPrice();
          gasPrice = gasPrice.mul(120).div(100);
          let gasLimit;
          if (isSaleOpen) {
            gasLimit = await contract.estimateGas.mint(
              res.result.count, res.result.signId, res.result.signature,
              { value, gasPrice, }
            );
            gasLimit = gasLimit.mul(135).div(100);
            tx = await contract.mint(
              res.result.count, res.result.signId, res.result.signature,
              { value, gasPrice, gasLimit, }
            );
          } else if (isPresalePaused) {
            gasLimit = await contract.estimateGas.mintRaffle(
              res.result.count, res.result.signId, res.result.signature,
              { value, gasPrice, }
            );
            gasLimit = gasLimit.mul(135).div(100);
            tx = await contract.mintRaffle(
              res.result.count, res.result.signId, res.result.signature,
              { value, gasPrice, gasLimit, }
            );
          } else if (isPresaleOpen) {
            gasLimit = await contract.estimateGas.mintRaffle(
              res.result.count, res.result.signId, res.result.signature,
              { value, gasPrice, }
            );
            gasLimit = gasLimit.mul(135).div(100);
            tx = await contract.mintRaffle(
              res.result.count, res.result.signId, res.result.signature,
              { value, gasPrice, gasLimit, }
            );
          }
          setMintReqStage('Waiting for Transaction confirmation');
          await tx.wait(1);
          setMintReqStage('Transaction confirmed');
          await pause(1000);
          closeAndShow();
          setMintReqStage(null);
          return;
        }
      }

      await pause(500);
      setMintReqStage(`Please wait for the next Whitelist. Don't reload the page.`);
    } catch (error) {
      console.error(error);
      if (typeof error === 'string' && error.inclides('Not authorized to mint'))
        setMintReqStage('Error: Not authorized to mint');
      else if (typeof error === 'string' && error.inclides('Signature used'))
        setMintReqStage('Error: Signature used');
      else if (typeof error === 'object' && error.error)
        setMintReqStage('Error: ' + error.error.message);
      else
        setMintReqStage('Error');
      await pause(5000);
      setMintReqStage(null);
    }
  }

  const [isWL, setWL] = useState(false);
  useEffect(async () => {
    if (account && !isSaleOpen && !isSoldOut) {
      try {
        const res = await api.isWhitelisted(account);
        if (res.ok) {
          if (res.result) toast('YOUR WALLET IS WHITELISTED', { autoClose: 5000 });
          return setWL(res.result);
        } else {
          setWL(false);
        }
      } catch (error) { setWL(false); }
    } else {
      setWL(false);
    }
  }, [account]);

  useEffect(() => {
    window.ym && window.ym(87029883, 'reachGoal', 'OpenMintModal');
  }, []);


  return (
    <Modal
      id={'MintModal'}
      consumer={children}
      isOpen={isOpen}
      setOpen={setOpen}
      draggable={false}
      duration={300}
    >
      <div className="modal-mask">
        <div className="modal-wrapper">
          <div className="modal-overlay" onClick={onClose}></div>
          <div className="modal-container">

            <div className="modal-img">
              <img src={rPopupImg} srcSet={`${rPopupX2Img} 2x`} width="432" height="432" alt="" title="" />
            </div>

            {isSoldOut && <MintModalBodySoldOut data={{ totalSupply, maxSupply }} />}

            {!isPresaleOpen && !isPresalePaused && !isSaleOpen && <MintModalBodySaleNotOpen data={{ totalSupply, maxSupply }} />}

            {((isPresaleOpen && !isWL) || (isPresalePaused && !isWL)) && !isSoldOut && !mintReqStage && <MintModalBodyNotWL data={{ totalSupply, maxSupply, stage }} />}

            {((isPresaleOpen && isWL) || (isPresalePaused && isWL) || isSaleOpen) && !isSoldOut && !mintReqStage && <div className="modal-body">
              <MintModalComponentHeader data={{ totalSupply, maxSupply, stage }} />

              {stage === 'RAFFLE' && <span style={{ marginTop: '-10px' }} >NEXT RAFFLE in <Timer startTime={nextRuffle} /></span>}
              <div className="modal-desc">Roborovski minted</div>


              {maxCountLimit > 0
                ? <>
                  <MintModalComponentSelect data={{ count, setCount, maxCountLimitTx, maxCountLimit }} />
                  <MintModalComponentPriceData data={{ count, priceEth, totalEth, balanceEth, }} />

                  <div className="modal-submit">
                    <ButtonPrimary tag="button" label="Mint Request" size="full" onClick={handlerMint} />
                  </div>
                </>
                : <>
                  <div className="modal-desc"><span style={{ color: '#fbbd74' }}>You have reached the address limit for this step.</span></div>
                  {(stage === 'PRESALE' || stage === 'RAFFLE') && <div className="modal-desc" style={{ color: '#fbbd74' }}>
                    {stage === 'PRESALE'
                      ? 'RAFFLE'
                      : 'SALE'
                    } in <Timer startTime={
                      stage === 'PRESALE'
                        ? TIMESTAMP_RAFFLE_2
                        : timestampSale
                    } />
                  </div>}
                </>}
            </div>}

            {mintReqStage && <div className="modal-body">
              <MintModalComponentHeader data={{ totalSupply, maxSupply, stage }} />
              <br />
              <div className="modal-desc"><span>{mintReqStage}</span></div>
              <div>Wait, please.</div>
            </div>}

            <button className="modal-close-button" onClick={onClose}><CloseIcon /></button>
          </div>
        </div>
      </div>
    </Modal>
  );
};

export default hot(module)(MintModal);
