import { hot } from 'react-hot-loader';
import { Link } from 'react-router-dom';
import LinkExternal from '../components/Link/LinkExternal';
import { nftMainnetAddress, rnctMainnetAddress } from '../configs';

import './ScreenTerms.scss';



function ScreenRnct() {
  return (
    <section className="section-terms">
      <div className="container" data-aos="fade-in" data-delay="200">
        <div className="pre-title">
          <Link className="pre-title-back" to="/">&larr; Go to homepage</Link>
        </div>

        <div className="row">
          <div className="col-12 col-lg-10 offset-lg-1 col-xl-8 offset-xl-2">
            <h1 className="title">RNCT Token</h1>
            <p></p>

            <br />
            <div className="textpage">
              <h2 className="title">What is it?</h2>
              <p>The Roborovski Name Change Token (RNCT) is the native token of the Roborovski NFT.
                The RNCT serves only one single purpose:
                It allows its holder to give their Roborovski NFT a unique name that is permanently
                stored and publicly visible on the Ethereum Blockchain.
                Thus, commoditizing the name itself and making it the rarest of all
                attributes within the entire project. This opens up a whole new dimension for collectibles where the value
                hierarchy of the individual pieces of the whole collective art is highly impacted by the preferences of the
                consumers.</p>
              <p>If you own a Roborovski NFT, you can claim the RNCTs that are accumulated by the NFT. Each NFT accumulates around
                3,660 RNCTs per year. If you participate in the initial contribution period, you receive an additional 1,830
                RNCTs. To change the name of an NFT, you need to send 1,830 RNCTs (about  years of NFT emission) to the Roborovski NFT
                contract and burn them.</p>

              <h2 className="title">The Rules</h2>
              <p><ul>
                <li>No name can be identical</li>
                <li>There is a limit of 25 symbols (including spaces)</li>
                <li>Uniqueness is case insensitive (i.e. John and john are considered the same for the blockchain)</li>
                <li>There are no leading or trailing spaces</li>
                <li>Only alphanumerical symbols are eligible for use</li>
                <li>Used names become available immediately after the name of the NFT was changed</li>
              </ul>
              </p>

              <h2 className="title">Emission Rate of RNCTs</h2>
              <p>Each year, approximately 3,660 RNCTs will be accumulated by each Roborovski NFT. You need 1,830 RNCTs to change the
                name of your Roborovski NFT. To give more deciding power over early participants, each participant in the first
                distribution period will receive an additional 1,830 RNCTs. After 10 years, the emission of new RNCTs
                comes to a halt. From then on, RNCTs can only be burnt until one day no RNCTs are left and the names of the
                Roborovski NFT cannot be altered anymore. The artwork is then considered complete. </p>
              <p>Number of RNCTs emitted per year per NFT: 3,660
                <br />Number of RNCTs required for one name change: 1,830
                <br />Bonus RNCTs for initial sales phase: 1,830
                <br /></p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default hot(module)(ScreenRnct);
