import React, { useState, useEffect } from 'react';
import { hot } from 'react-hot-loader';




//const now = () => Math.trunc(Date.now() / 1000);
function getTimeRemaining(endtime) {
  //const total = Date.parse(endtime) - Date.parse(new Date());
  const total = endtime * 1000 - Date.parse(new Date());
  const seconds = Math.floor((total / 1000) % 60);
  const minutes = Math.floor((total / 1000 / 60) % 60);
  const hours = Math.floor((total / (1000 * 60 * 60)) % 24);
  const days = Math.floor(total / (1000 * 60 * 60 * 24));

  return {
    'total': total,
    'days': days,
    'hours': hours,
    'minutes': minutes,
    'seconds': seconds
  };
}

function numZeroPad(num) {
  if (num < 10)
    return `0${num}`;
  else
    return `${num}`;
}

function Timer({ startTime }) {
  const [timeInit, setTimeInit] = useState(startTime);
  const [intervalId, setIntervalId] = useState(null);
  const [timer, setTimer] = useState({ 'total': -1, 'days': 0, 'hours': 0, 'minutes': 0, 'seconds': 0, });
  const updateClock = () => {
    let timer = getTimeRemaining(startTime);
    setTimer(timer);

    if (new Date(startTime * 1000).getTime() <= Date.now()) {
      clearInterval(intervalId);
      return;
    };
  }
  useEffect(() => {
    if (startTime !== timeInit) {
      setTimeInit(startTime);
      let timer = getTimeRemaining(startTime);
      setTimer(timer);
    }
  }, [startTime]);

  useEffect(() => {
    updateClock();
    const iId = setInterval(() => {
      updateClock();
    }, 1000);
    setIntervalId(iId);
    return () => {
      clearInterval(intervalId);
    };
  }, []);

  if (timer.total > 0) {
    return (
      <span>
        {numZeroPad(timer.hours)}:{numZeroPad(timer.minutes)}:{numZeroPad(timer.seconds)}
      </span>
    );
    return (
      <h2>
        {numZeroPad(timer.days)} Days {numZeroPad(timer.hours)} Hours {numZeroPad(timer.minutes)} Minutes {numZeroPad(timer.seconds)} Seconds
      </h2>
    );
  }

  return null;
}

export default hot(module)(Timer);
